'use strict';
//{
//   code:0,
//   response:{
//     resultCode: 0 //结果状态（0-成功，1-失败，-1 网络失败，-2 资源解压失败）
//     size: '1080_1920' //1080_1920或者 640_960
//   }
// }
import {WinJSDec, appError, fnError} from './APP/config';

var name = 'GET_FLOOR';
var GET_FLOOR = (fn=_=>{}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      window.WinJSBridge.call('events', 'size', function (res) {
        var {code, response} = res;
        if (code == 0) {
          fn(response);
        } else {
          throw new Error(`[${name}] ${appError}`);
        }
      });
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default GET_FLOOR;